import React, { useState } from "react";
import { Link } from "react-router-dom";

const ReviewComments = () => {
  const ReadMore = ({ children }) => {
    const text = children;
    const [isReadMore, setIsReadMore] = useState(true);
    const toggleReadMore = () => {
      setIsReadMore(!isReadMore);
    };

    return (
      <p className="text">
        {isReadMore ? text.slice(0, 150) : text}
        <span onClick={toggleReadMore} className="read-or-hide">
          {isReadMore ? (
            <span
              style={{
                color: "#24902f",
                fontWeight: "bold",
                cursor: "pointer",
              }}
            >
              Read more
            </span>
          ) : (
            <span style={{ color: "#2790aa", cursor: "pointer" }}>
              Show Less
            </span>
          )}
        </span>
      </p>
    );
  };
  return (
    <>
      <div className="container mb-3">
        <div className="row" style={{ borderBottom: "1px solid #e5e5e5" }}>
          <div className="col-lg=3">
            <img
              src="/img/men.jpeg"
              alt=""
              style={{
                borderRadius: "50%",
                height: "48px",
                width: "48px",
                display: "block",
              }}
            />
            <span
              className="text-secondary mt-1 lh-1"
              style={{ display: "block" }}
            >
              Micheal Jackson
            </span>
            <span className="text-secondary" style={{ display: "block" }}>
              30 Mar 2021
            </span>
            <div className="rating-summary mb-3">
              <Link to="#">
                <i className="fa fa-star"></i>
              </Link>
              <Link to="#">
                <i className="fa fa-star"></i>
              </Link>
              <Link to="#">
                <i className="fa fa-star"></i>
              </Link>
              <Link to="#">
                <i className="fa fa-star"></i>
              </Link>
              <Link to="#">
                <i className="fa fa-star"></i>
              </Link>
            </div>
          </div>
          <div className="col-lg-8 ml-5 mt-2">
            <ReadMore>
              আমি হুমায়ুন আহমেদের বিশাল ভক্তশ্রেণীর একজন নই । তবে দেয়াল
              উপন্যাসটি এককথায় অনবদ্য । এর প্রধান কারণ হল,উপন্যাসের পটভূমি রচিত
              হয়েছে সেই সময়টিকে ঘিরে,যাকে নিয়ে প্রচুর জল্পনা থাকলেও লেখা খুব
              বেশি নেই । একজন ঔপন্যাসিকের দৃষ্টিতে তত্‍কালিন সোশ্যো-পলিটিকাল
              পরিস্থিতির চিত্রায়ন সত্যিই অসাধারণ । এর মাধ্যমে আরেকটি জিনিস সবার
              জানার সুযোগ হয়েছে । সেটি হল ইতিহাসের প্রতিশোধ । বঙ্গবন্ধু ও
              চারনেতা হত্যার পেছনের মানুষগুলা কিন্তু কেউ রেহাই পায়নি । নিজেরাই
              নিজেদের একে একে নিশ্চিহ্ন করেছে । ইতিহাস এভাবেই সমতা রক্ষা করে ।
            </ReadMore>
          </div>
        </div>
      </div>
    </>
  );
};

export default ReviewComments;
